@import "https://fonts.googleapis.com/css2?family=Chakra+Petch:wght@300&display=swap";
* {
  box-sizing: border-box;
}

body {
  color: #fff;
  min-height: 100vh;
  background-color: #5e5e5e;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
  font-family: Chakra Petch, sans-serif;
  display: flex;
}

p {
  margin: 5px 0;
}

h2 {
  text-align: center;
  margin: 10px 0 20px;
}

input[type="checkbox"] {
  filter: sepia() brightness(80%) hue-rotate(200deg) saturate(70%) contrast(300%);
  margin-right: 0;
}

input[type="number"] {
  background-color: #f2ecff;
  border-radius: 8px;
  padding-left: 5px;
}

.container {
  width: 350px;
  max-width: 100%;
  background-color: #909090;
  border-radius: 50px;
  padding: 20px;
  box-shadow: 0 2px 10px #fff3;
}

.result-container {
  letter-spacing: 1px;
  height: 50px;
  width: 100%;
  background-color: #0006;
  border-radius: 25px;
  justify-content: flex-start;
  align-items: center;
  padding: 12px 10px;
  font-size: 18px;
  display: flex;
  position: relative;
}

.result-container #result {
  word-wrap: break-word;
  max-width: calc(100% - 40px);
}

.result-container .btn {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  font-size: 20px;
  position: absolute;
  top: 5px;
  right: 5px;
}

.btn {
  color: #333;
  cursor: pointer;
  background-color: #f2ecff;
  border: none;
  padding: 8px 12px;
  font-size: 16px;
}

.btn:hover, .btn:focus {
  background-color: #cec8db;
  transition: all .3s ease-out;
}

.btn-large {
  width: 100%;
  border-radius: 20px;
  display: block;
}

.setting {
  justify-content: space-between;
  align-items: center;
  margin: 15px 0;
  display: flex;
}

@media screen and (max-width: 400px) {
  .result-container {
    font-size: 14px;
  }
}

.social-panel-container {
  transition: transform .4s ease-in-out;
  position: fixed;
  bottom: 80px;
  right: 0;
  transform: translateX(100%);
}

.social-panel-container.visible {
  transform: translateX(-10px);
}

.social-panel {
  height: 169px;
  width: 370px;
  max-width: calc(100% - 10px);
  background-color: #fff;
  border: 5px solid #001f61;
  border-radius: 16px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: Muli;
  display: flex;
  position: relative;
  box-shadow: 0 16px 31px -17px #001f6199;
}

.social-panel button.close-btn {
  color: #97a5ce;
  cursor: pointer;
  border: 0;
  font-size: 20px;
  position: absolute;
  top: 5px;
  right: 5px;
}

.social-panel button.close-btn:focus {
  outline: none;
}

.social-panel p {
  color: #fff;
  text-align: center;
  width: 235px;
  background-color: #001f61;
  border-radius: 0 0 10px 10px;
  margin: 0;
  padding: 2px 17px 6px;
  font-size: 14px;
  line-height: 18px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.social-panel p i {
  margin: 0 5px;
}

.social-panel p a {
  color: #ff7500;
  text-decoration: none;
}

.social-panel h4 {
  color: #97a5ce;
  text-transform: uppercase;
  margin: 20px 0;
  font-family: Muli;
  font-size: 14px;
  line-height: 18px;
}

.social-panel ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.social-panel ul li {
  margin: 0 10px;
}

.social-panel ul li a {
  color: #001f61;
  height: 50px;
  width: 50px;
  border: 1px solid #dce1f2;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  text-decoration: none;
  display: flex;
}

.social-panel ul li a:hover {
  border-color: #ff6a00;
  box-shadow: 0 9px 12px -9px #ff6a00;
}

.floating-btn {
  color: #fff;
  cursor: pointer;
  background-color: #001f61;
  border: 1px solid #001f61;
  border-radius: 26.5px;
  padding: 12px 20px;
  font-size: 16px;
  line-height: 20px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  box-shadow: 0 16px 22px -17px #03153b;
}

.floating-btn:hover {
  color: #001f61;
  background-color: #fff;
}

.floating-btn:focus {
  outline: none;
}

.floating-text {
  color: #fff;
  text-align: center;
  background-color: #001f61;
  border-radius: 10px 10px 0 0;
  padding: 7px 15px;
  font-family: Muli;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.floating-text a {
  color: #ff7500;
  text-decoration: none;
}

@media screen and (max-width: 480px) {
  .social-panel-container.visible {
    transform: translateX(0);
  }

  .floating-btn {
    right: 10px;
  }
}

